/******************************************************************
  - MIXINS -
******************************************************************/
.content-panel {
  padding: 0rem 0 3rem;
  padding-top: 0;
}
.content-panel_top {
  margin-bottom: 3rem;
}
.content-panel_title {
  text-align: center;
}
.content-panel_title h2 {
  margin-top: 3rem;
  margin-bottom: 0;
}
.content-panel_container {
  margin-bottom: 3rem;
}
.content-panel_container.even {
  height: calc(100% - 3rem);
}
.content-panel_container.even .content-panel_container-media {
  height: 314px;
  position: relative;
}
.content-panel_container.even .content-panel_container-media img, .content-panel_container.even .content-panel_container-media video {
  height: 100%;
  width: 100%;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  -o-object-fit: cover;
     object-fit: cover;
  -o-object-position: 50% 50%;
     object-position: 50% 50%;
  font-family: "object-fit";
}
.content-panel_container.even .content-panel_container-media:empty {
  height: 0 !important;
}
.content-panel_container-media {
  margin-bottom: 2rem;
  overflow: hidden;
  position: relative;
}
.content-panel_container-media video, .content-panel_container-media img {
  width: 100%;
  -webkit-transition: -webkit-transform 0.3s;
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
}
.content-panel_container-media:after {
  content: " ";
  display: block;
  position: absolute;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0);
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.content-panel_container-media:hover:after {
  background: rgba(0, 0, 0, 0.2);
}
.content-panel_container-media:hover img {
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
}
.content-panel_container-media:empty {
  height: 0;
}
.content-panel_container-content h4 {
  margin-bottom: 2rem;
}
.content-panel_container-content h4 a {
  color: #1D1D1B;
}
.content-panel_container-content_controls {
  margin-top: 0.875rem;
  margin-bottom: 0;
}
.content-panel_container-content_controls p a.arrowlink {
  font-weight: bold;
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
  -webkit-justify-content: flex-start;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -moz-flex-direction: row;
  -webkit-flex-direction: row;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}
.content-panel_container-content_controls p a.arrowlink svg {
  margin-right: 10px;
}
.content-panel_container-content_controls:empty {
  margin: 0;
}
.content-panel.bordered .content-panel_container {
  background: #ffffff;
  border-top: 1px solid #F6F6F6;
  -webkit-box-shadow: 0 2px 3px 0px rgba(0, 0, 0, 0.15);
          box-shadow: 0 2px 3px 0px rgba(0, 0, 0, 0.15);
}
.content-panel.bordered .content-panel_container-media {
  margin-bottom: 0;
}
.content-panel.bordered .content-panel_container-content {
  padding: 2rem;
}
.content-panel.bordered .content-panel_container-content_controls {
  margin: 1.5rem 0 0;
}
.content-panel.bordered .content-panel_container-content_controls p, .content-panel.bordered .content-panel_container-content_controls .button {
  margin: 0;
}

@media print, screen and (min-width: 45em) {
  .content-panel {
    padding: 6rem 0;
    padding-top: calc(6rem - 30px);
  }
  .content-panel_title {
    text-align: center;
  }
  .content-panel_title h2 {
    margin-top: 0;
    margin-bottom: 1rem;
  }
}